import {NumericUtils} from "../Util/NumericUtils"
export class ColorsModel{
	constructor(){
	
	}
	
	generatePlusTasks(num,smpNum){
		return Array(num).fill().map((_, i) => {
			let a = NumericUtils.getRandomInt(0,9);
			let b = NumericUtils.getRandomInt(0,9 - a);
			let sum = a + b;
			return {
				type:"+",
				index:i,
				a:a,
				b:b,
				result:sum,
				samples:this.generateSample(smpNum,sum)
			}
		})
	}
	
	generateSample(num,res){
		let x = NumericUtils.shuffle(Array(10).fill().map((_, i) => i)).slice(0,num);
		return (x.indexOf(res) >= 0)?x:this.generateSample(num,res);
	}
}
