<template>
	<el-carousel ref="c1" v-if="tasks" :interval="4000" :autoplay="false" type="card" height="200px" :loop="false"  trigger="click">
		<el-carousel-item class="car-item"  v-for="(t,i) in tasks" v-bind:key="i">
			<problem @correct="next" v-bind:definition="t" v-bind:colors="colors"></problem>
		</el-carousel-item>
	</el-carousel>
</template>

<script>
	import Problem from "./problem";
	export default {
		name: "task",
		components: {Problem},
		data(){
			return {
			
			}
		},
		props:["tasks","colors"],
		methods:{
			next(){
				setTimeout(() => this.$refs.c1.next(),1)
			}
		},
		watch:{
			tasks(){
				setTimeout(() => this.$refs.c1.setActiveItem(0));
			}
		}
	}
</script>

