<template>
	<el-container>
		<el-header>
			<el-button  @click="generateTasks()">Run</el-button>
			<controls v-bind:colors="colors"></controls>
		</el-header>
		<el-main>
			<task v-bind:tasks="tasks" v-bind:colors="colors"></task>
		</el-main>
	</el-container>
</template>

<script>
	import { D3Wrapper } from "./../../Util/D3Wrapper";
	import { ColorsModel } from "./../../models/ColorsModel";
	import Controls from "./controls";
	import Task from "./task";
	export default {
		name: "colors",
		components: {Task, Controls},
		data(){
			return{
				colors:D3Wrapper.d3.schemeCategory10,
				model:null,
				tasks:[]
			}
		},
		created(){
			this.model = new ColorsModel();
		},
		methods:{
			generateTasks(){
				this.tasks = this.model.generatePlusTasks(20,4)
			}
		}
	}
</script>

