<template>
	<el-container style="flex-direction: column">
		<div style="margin: 0 auto;width: 20px">
			<i v-if="res == 'correct'" class="el-icon-success x-cor" style="color: green"></i>
			<i v-if="res == 'incorrect'" class="el-icon-error x-cor" style="color: red"></i>
			<i v-if="res == null" class="el-icon-question x-cor" style="color: silver"></i>
		</div>
		<el-container class="problem" style="padding: 30px 15px;width: 525px;margin: -20px auto;">
			<el-card class="param" v-bind:style="{backgroundColor:colors[definition.a]}"><span v-bind:class="{ invis: !visible }">{{definition.a}}</span></el-card>
			<el-card class="param sign">{{definition.type}}</el-card>
			<el-card class="param" v-bind:style="{backgroundColor:colors[definition.b]}"><span v-bind:class="{ invis: !visible }">{{definition.b}}</span></el-card>
			<el-card class="param sign">=</el-card>
			<div style="display: flex;padding: 4px 3px;border: 1px solid gray;border-radius: 7px;margin: 4px;">
				<el-card @click.native="onAnswer(s)"  class="variant" v-for="s in definition.samples" v-bind:style="{backgroundColor:colors[s]}" v-bind:key="s"><span v-bind:class="{ invis: !visible }">{{s}}</span></el-card>
			</div>
		</el-container>
	</el-container>
</template>

<script>
	export default {
		name: "problem",
		props:["definition","colors"],
		data(){
			return{
				visible:false,
				res:null
			}
		},
		methods:{
			onAnswer(x){
				if(this.definition.result == x){
					this.$emit("correct")
					this.res = "correct"
				}else{
					this.$emit("incorrect");
					this.res = "incorrect"
					this.visible = true;
				}
			}
		},
		watch:{
			definition(){
				this.visible = false;
				this.res = null;
			}
		}
	}
</script>

<style scoped>
	.problem{
		margin: 20px 10px;
	}
	.problem .param{
			margin: 5px;
		padding: 4px;
		}
	.problem .sign{
			background-color: transparent;
			font-weight: bold;
		}
	.problem .variant{
		margin: 0 1px;
	}
	.problem .invis{
		visibility: hidden;
	}
	.problem span{
		color: white;
		font-weight: bold;
	}
	
	.x-cor{
		text-align: center;
		font-size: 40px;
		line-height: 50px;
	}

</style>
